@import url(https://fonts.googleapis.com/css2?family=Fondamento&display=swap);
body {
  margin: 0;
  font-family: 'Fondamento',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: lightgray;
  font-size: x-large;
 
}
html {
  overflow: hidden;
  overflow-y: scroll;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h2 {
  color: #6d4c41;
  font-weight: bold;
}
h4 {
  color:  #6d4c41;
  font-weight: bold;
}

#navbar {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
}
@media only screen and (max-width: 800px) {
  #navbar {
      position: fixed;
      top: 0;
      width: 100%;
      z-index: 1;
  }
}

.activites {
  background-color:  lightgray;
  border-color: grey;
}
